$themeColours: (
  "primary": $theme-primary,
  "secondary": $theme-secondary,
  "red": $alert-danger,
  "yellow": $color-yellow
);
body {
  background-color: $theme-secondary;
}
header.header {
  background-color: $theme-secondary;
}
footer.footer {
  color: $color-white;
  background-color: $theme-primary;
}

a {
  color: $theme-link-color;
}


.sub-footer-container {
  background-color:  $theme-secondary;
}